import React, { useState } from 'react';

const Contact = () =>{
  const [data, setData] = useState({
    fullname: '',
    number: "",
    email: "",
    msg: "",
  });

  const inputEvent = (event) =>{
    const {name, value} = event.target;
    setData((preVal) =>{
      return {
        ...preVal,
        [name] : value,
      };
    });
  };
  const formSubmit= (e) =>{
    e.preventDefault();
    alert(`Your Name: ${data.fullname} Your P.no: ${data.number} Your Email: ${data.email} Your Msg: ${data.msg}`);

  }
  return (
    <>
      <div className="my-5">
        <h1 className="text-center">Contact Us</h1>
      </div>
      <div className="container contact_div">
        <div className="row">
          <div className="col-md-6 col-10 mx-auto">
             <form onSubmit={formSubmit}>
             <div class="mb-3">
  <label for="exampleFormControlInput1" class="form-label">Full Name</label>
  <input required type="text" class="form-control" id="exampleFormControlInput1" name="fullname" value={data.fullname} onChange={inputEvent} placeholder="Enter your full name" />
</div>
 <div class="mb-3">
  <label for="exampleFormControlInput1" class="form-label">Phone Number</label>
  <input required type="number" class="form-control" id="exampleFormControlInput1" name="number" value={data.number} onChange={inputEvent} placeholder="Enter your phone number" />
</div>
 <div class="mb-3">
  <label for="exampleFormControlInput1" class="form-label">Email address</label>
  <input required type="email" class="form-control" id="exampleFormControlInput1" name="email" value={data.email} onChange={inputEvent} placeholder="name@example.com" />
</div>
<div class="mb-3">
  <label for="exampleFormControlTextarea1" class="form-label">Message</label>
  <textarea required class="form-control" id="exampleFormControlTextarea1" rows="3" name="msg" value={data.msg} onChange={inputEvent}></textarea>
</div>
  <div class="col-12">
    <button class="btn btn-outline-primary" type="submit" >Submit form</button>
  </div>
             </form>
          </div>
        </div>
      </div>
    </>
  )
}
export default Contact;